:root {
  --btn-color: #3699d3;
  --btn-text-color: rgb(226, 229, 226);
  --btn-action-color: #ab8757;
  --btn-action-text-color: rgb(226, 229, 226);
  --screen-back-color: rgb(197 206 209);
  --screen-text-color: rgb(80, 86, 88);
}
.digit {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 70px;
  height: 70px;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  background-color: var(--btn-color);
  font-family: 'Roboto';
  font-size: 2rem;
  color: var(--btn-text-color);
}
.digit:hover {
  opacity: 0.45;
}
.sub {
  font-size: 0.8rem;
  color: rgb(216, 214, 214);
  margin: -4px auto;
  transform: translateY(-5px);
  pointer-events: none;
}

.back {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 15%;
}
.round_button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 70px;
  height: 70px;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  background-color: var(--btn-action-color);
  color: var(--btn-action-text-color);
}
.round_button span {
  pointer-events: none;
}
.round_button i {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  color: #fff;
  transform: scale(2);
}
.round_button[disabled] {
  filter: brightness(50%);
}

/* .round_button i:hover, */
.round_button:not([disabled]):hover {
  opacity: 0.8;
}
.round_button:not([disabled]):active,
.digit:active {
  box-shadow: inset 0 -3em 3em rgba(0, 0, 0, 0.1), 0 0 0 2px rgb(87, 44, 44),
    0.3em 0.3em 1em rgba(0, 0, 0, 0.3);
}

.back i {
  font-size: 20px;
  color: #333;
  cursor: pointer;
}

.back:active {
  opacity: 0.5;
}

.incall_name {
  font-family: 'Roboto';
  font-size: 20pt;
}
.incall_number {
  font-size: 15pt;
}
.incall_time {
  font-size: 20pt;
}
.bi {
  cursor: pointer;
}
.action-button {
  display: flex;
  width: 20px;
  height: 20px;
  cursor: pointer;
}
.action-button i {
  pointer-events: none;
}

.park-number {
  color: var(--screen-text-color);
}
.park-timer {
  color: var(--screen-text-color);
}

.active-number {
  font-family: 'Roboto';
  font-size: 2rem;
  color: var(--screen-text-color);
}
.active-timer {
  color: var(--screen-text-color);
}
