/* Define Background coloe */
:root {
  --background: #39aff3;
  --background2: white;
  --bs-table-hover-color: green;
}

/* Custom Scrool bar */
::-webkit-scrollbar {
  width: 3px;
}

::-webkit-scrollbar-track {
  width: 3px;
  background-color: rgba(117, 96, 96, 0.3);
  height: 50px;
}

::-webkit-scrollbar-thumb {
  width: 5px;
  height: 20px;
  background-color: rgba(29, 25, 25, 0.3);
}

/* Bootstrap Customization */
.table tbody tr {
  user-select: none;
}

.his-table-dir {
  width: 60px;
  text-align: center;
}
.his-table-num {
  width: 150px;
}
.his-table-time {
  width: 140px;
}
.his-table-dur {
  width: 50px;
}
